import { h, Component } from 'preact';
import PropTypes from 'prop-types';
import style from './style.scss';
import { connect } from 'preact-redux';
import Octicon from '../../components/octicon';

export class Notification extends Component {
  propTypes = {
    iconName: PropTypes.string,
    data: PropTypes.object,
    onClose: PropTypes.func
  };

  componentDidMount() {
    setTimeout(() => {
      this.close();
    }, this.props.data.duration || 5000);
  }

  close = () => {
    this.props.onClose(this.props.data.id);
  };

  capitalizeFirstLetter(string) {
    if (!string) {
      return string;
    }
    return string.charAt(0).toUpperCase() + string.slice(1);
  }

  mapTypeToIconName(type) {
    switch (type) {
      case 'error':
        return 'thumbsdown';
      default:
        return '';
    }
  }

  render(props) {
    const iconName = this.mapTypeToIconName(props.data.type);
    return (
      <div className={style.notification + ' ' + style[props.data.type]}>
        <div className={style.header}>
          <Octicon className={style.icon} name={iconName} />
          <h3 className={style.headline}>
            {this.capitalizeFirstLetter(props.data.type)}
          </h3>
        </div>
        {props.data.messages.map((message, j) => (
          <p key={`msg${j}`}>{message}</p>
        ))}
        <Octicon onClick={this.close} className={style.closeIcon} name="x" />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    notificationStack: state.home.notificationStack
  };
};

export default connect(mapStateToProps)(Notification);



