import crawl from 'tree-crawl';

const initialState = {
  data: [],
  activeVideo: {
    meta: {},
    src: ''
  },
  editMode: false,
  searchResults: null,
  notificationStack: []
};

const reducer = (state = initialState, action) => {
  console.log('reducer', action);
  switch (action.type) {
    case 'SET_ACTIVE_VIDEO':
      return {
        ...state,
        activeVideo: action.video
      };
    case 'SET_EDIT_MODE':
      return {
        ...state,
        editMode: action.editing
      };
    case 'SAVING_META':
      return {
        ...state,
        data: setNewMetaInTree({ ...state.data }, action.newMeta)
      };
    case 'RETRIEVE_DATA_SUCCESS':
    case 'SAVE_META_SUCCESS':
      return {
        ...state,
        data: action.data
      };
    case 'SAVE_META_FAILURE':
      return {
        ...state,
        notificationStack: [
          ...state.notificationStack,
          {
            id: Date.now(),
            type: 'error',
            messages: ['Data could not be saved on the server']
          }
        ]
      };
    case 'POP_NOTIFICATION_STACK':
      return {
        ...state,
        notificationStack: state.notificationStack.filter(
          notification => notification.id !== action.id
        )
      };

    case 'SET_SEARCH_RESULTS':
      return {
        ...state,
        searchResults: action.data
      };
    default:
      return state;
  }
};

const setNewMetaInTree = (tree, newMeta) => {
  crawl(
    tree,
    (node, context) => {
      if (node.meta && node.meta.id === newMeta.id) {
        const newNode = node;
        newNode.meta = newMeta;

        context.parent.items[context.index] = newNode;
        context.replace(newNode);
      }
    },
    { getChildren: node => node.items }
  );
  return tree;
};

export default reducer;



