import { h, Component } from 'preact';
import PropTypes from 'prop-types';
import style from './style.scss';
import { connect } from 'preact-redux';
import Notification from '../../components/notification';
import { popNotificationStack } from './actions.js';

export class NotificationWrapper extends Component {
  propTypes = {
    onCloseNotification: PropTypes.func,
    notificationStack: PropTypes.array
  };

  onClose = id => {
    this.props.onCloseNotification(id);
  };

  render(props) {
    return (
      props.notificationStack.length > 0 && (
        <div className={style.wrapper}>
          {props.notificationStack.map(data => (
            <Notification
              onClose={this.onClose}
              key={`${data.id}note`}
              data={data}
            />
          ))}
        </div>
      )
    );
  }
}

const mapStateToProps = state => {
  return {
    notificationStack: state.home.notificationStack
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onCloseNotification: id => dispatch(popNotificationStack(id))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(
  NotificationWrapper
);



